/* O .places-container herda do .module-container, mas podemos ajustar */
.places-container {
    background-color: #f0f8ff; /* Fundo Azul Gelo (AliceBlue) */
    border: 2px solid #b0e0e6; /* Borda Azul (PowderBlue) */
    max-width: 800px;
    text-align: center;
}

.places-title {
    font-family: 'Dancing Script', cursive;
    font-size: 2.8em;
    font-weight: 700;
    color: #005691; /* Azul Escuro */
    margin-bottom: 15px;
}

.places-list {
    margin-top: 20px;
    max-height: 50vh; /* Altura m√°xima para a lista */
    overflow-y: auto; /* Scroll se a lista for longa */
    padding: 10px;
}

.place-item {
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
    border: 1px solid #ddd;
    /* üö® ADICIONADO: Necess√°rio para posicionar o bot√£o "X" */
    position: relative;
}

.place-item h3 {
    margin: 0 0 5px 0;
    color: #005691;
}

.place-notes {
    font-style: italic;
    color: #555;
    margin: 0 0 15px 0;
}

/* --- Estilo das Estrelas --- */
.star-rating {
    display: flex;
    justify-content: center;
    gap: 5px;
}

.star {
    font-size: 2.5em; /* Tamanho das estrelas */
    color: #ccc; /* Cor da estrela vazia (cinza) */
    cursor: pointer;
    transition: color 0.2s, transform 0.1s;
}

.star.filled {
    color: #FFD700; /* Cor da estrela cheia (dourado) */
}

.star:hover {
    transform: scale(1.1);
}

/* --- Estilos do Formul√°rio de Adi√ß√£o --- */

.add-place-form {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    border: 1px solid #ddd;
    margin-bottom: 25px;
    display: flex;
    flex-direction: column;
    gap: 10px; /* Espa√ßo entre os campos */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
}

.add-place-form h3 {
    margin: 0 0 10px 0;
    color: #005691;
    text-align: center;
}

.add-place-form input,
.add-place-form textarea {
    width: 100%;
    padding: 12px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-family: 'Inter', sans-serif;
    font-size: 1em;
    box-sizing: border-box; /* Garante que o padding n√£o quebre o layout */
}

.add-place-form textarea {
    resize: vertical; /* Permite redimensionar apenas verticalmente */
    min-height: 80px;
}

/* Estilo para o bot√£o de adicionar */
.add-button {
    background-color: #0077be; /* Azul do tema */
    color: white;
    font-weight: bold;
}

.add-button:hover {
    background-color: #005691;
}

/* --- üö® NOVOS ESTILOS PARA O BOT√ÉO DE EXCLUIR üö® --- */

.delete-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #ffcccc; /* Fundo vermelho claro */
    color: #a00000; /* Vermelho escuro */
    border: 1px solid #ffaaaa;
    border-radius: 50%; /* C√≠rculo */
    width: 30px;
    height: 30px;
    font-size: 1.2em;
    font-weight: bold;
    line-height: 1; /* Alinha o "X" verticalmente */
    cursor: pointer;
    transition: all 0.2s;
    padding: 0; /* Remove padding padr√£o */
    z-index: 5; /* Garante que fique acima de outros elementos se houver */
}

.delete-button:hover {
    background-color: #ffaaaa;
    color: #000;
    transform: scale(1.1);
}
/* --- üö® NOVOS ESTILOS PARA O MODAL DE CONFIRMA√á√ÉO üö® --- */

/* O fundo escurecido (overlay) */
.confirm-modal-overlay {
    position: fixed; /* Cobre a tela inteira */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6); /* Sombra preta semi-transparente */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100; /* Garante que fique acima de tudo */
    animation: fadeInModal 0.3s ease;
}

/* Anima√ß√£o de fade-in para o overlay */
@keyframes fadeInModal {
    from { opacity: 0; }
    to { opacity: 1; }
}

/* A caixa de di√°logo (o modal em si) */
.confirm-modal-box {
    background-color: #fff;
    padding: 25px 30px;
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 450px;
    text-align: center;
    animation: slideInModal 0.3s ease-out;
}

/* Anima√ß√£o de slide-in para a caixa */
@keyframes slideInModal {
    from { transform: translateY(-30px); opacity: 0; }
    to { transform: translateY(0); opacity: 1; }
}

.confirm-modal-box h3 {
    font-family: 'Dancing Script', cursive; /* Fonte rom√¢ntica */
    font-size: 2em;
    color: #d6336c; /* Cor rosa */
    margin: 0 0 10px 0;
}

.confirm-modal-box p {
    font-size: 1.1em;
    color: #333;
    margin-bottom: 25px;
}

.modal-buttons {
    display: flex;
    justify-content: center;
    gap: 15px;
}

/* Estilo base para os bot√µes do modal */
.modal-btn {
    padding: 10px 20px;
    border: none;
    border-radius: 8px;
    font-size: 1em;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.2s;
}

/* Bot√£o de Cancelar (mais sutil) */
.modal-btn.cancel {
    background-color: #eee;
    color: #555;
    border: 1px solid #ccc;
}
.modal-btn.cancel:hover {
    background-color: #ddd;
}

/* Bot√£o de Confirmar (destaque vermelho) */
.modal-btn.confirm {
    background-color: #e63946; /* Vermelho perigo */
    color: white;
}
.modal-btn.confirm:hover {
    background-color: #c91825;
}
/* --- üö® NOVOS ESTILOS PARA ABAS E SELETOR üö® --- */
/* (Adicione ao final do seu VisitedPlaces.css) */

.category-tabs {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
}

.tab-btn {
    padding: 10px 15px;
    font-size: 1em;
    font-weight: 600;
    border: 2px solid #b0e0e6; /* Borda Azul (PowderBlue) */
    background-color: #fff;
    color: #005691; /* Azul Escuro */
    border-radius: 20px;
    cursor: pointer;
    transition: all 0.2s;
}

.tab-btn:hover {
    background-color: #f0f8ff; /* Azul Gelo (hover) */
}

/* Estilo da aba ativa */
.tab-btn.active {
    background-color: #0077be; /* Azul do tema */
    color: #fff;
    border-color: #0077be;
}

/* Estilo para o <select> no formul√°rio */
.add-place-form select {
    width: 100%;
    padding: 12px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-family: 'Inter', sans-serif;
    font-size: 1em;
    box-sizing: border-box;
    background-color: #fff;
    cursor: pointer;
}

/* Mensagem de lista vazia */
.empty-list-message {
    font-style: italic;
    color: #555;
    padding: 20px 0;
    text-align: center;
}