@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Inter:wght@400;600&display=swap');

/* Estilo para o container principal */
.main-app-container {
    padding: 40px;
    min-height: 100vh;
    color: #2c2c2c;
    font-family: 'Inter', sans-serif;
    animation: fadeIn 1s ease-out;
    background: #55b6ff;
    overflow: hidden;
    position: relative;
    /* Garante que o conte√∫do (como o t√≠tulo) comece no topo */
    display: block;
}

/* üö® NOVA REGRA: Centraliza o m√≥dulo do Quiz (ou qualquer outro) na tela */
.main-app-container.module-active {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

/* EFEITO DE LUZ E AREIA */
.main-app-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at center, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0) 70%);
    opacity: 0.8;
    animation: sunRipple 15s linear infinite;
    z-index: 0;
    pointer-events: none; /* üëà CORRE√á√ÉO IMPORTANTE */
}

@keyframes sunRipple {
    0% { background-size: 200% 200%; background-position: 0% 0%; }
    100% { background-size: 250% 250%; background-position: 100% 100%; }
}

/* --- üö® RE-ADICIONADO: ESTILO PARA PEIXES E CORA√á√ïES --- */

.floating-elements {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 1;
    overflow: hidden;
}

/* Base para cada part√≠cula flutuante (peixe ou cora√ß√£o) */
.floating-elements span {
    position: absolute;
    top: 110%;
    opacity: 0;
    animation: drift 20s linear infinite;
    z-index: 1; /* Garante que fique acima do efeito de luz */
}

/* Keyframe da anima√ß√£o: subir e balan√ßar */
@keyframes drift {
    0% {
        transform: translateY(0) translateX(0) rotate(0deg);
        opacity: 0;
    }
    10% {
        opacity: 0.7; /* Aparece */
    }
    90% {
        opacity: 0.7; /* Permanece vis√≠vel */
    }
    100% {
        transform: translateY(-110vh) translateX(10vw) rotate(15deg);
        opacity: 0;
    }
}

/* Espalhando CADA part√≠cula individualmente */
.floating-elements span:nth-child(1) { left: 5%; font-size: 30px; animation-duration: 25s; animation-delay: 0s; }
.floating-elements span:nth-child(2) { left: 15%; font-size: 20px; animation-duration: 20s; animation-delay: 3s; transform: translateX(-5vw); }
.floating-elements span:nth-child(3) { left: 80%; font-size: 40px; animation-duration: 18s; animation-delay: 1s; }
.floating-elements span:nth-child(4) { left: 30%; font-size: 25px; animation-duration: 22s; animation-delay: 5s; transform: translateX(-3vw); }
.floating-elements span:nth-child(5) { left: 90%; font-size: 30px; animation-duration: 28s; animation-delay: 2s; }
.floating-elements span:nth-child(6) { left: 50%; font-size: 22px; animation-duration: 19s; animation-delay: 7s; }
.floating-elements span:nth-child(7) { left: 65%; font-size: 35px; animation-duration: 23s; animation-delay: 4s; }
.floating-elements span:nth-child(8) { left: 40%; font-size: 28px; animation-duration: 21s; animation-delay: 6s; transform: translateX(-2vw); }
.floating-elements span:nth-child(9) { left: 75%; font-size: 20px; animation-duration: 26s; animation-delay: 8s; }
.floating-elements span:nth-child(10) { left: 20%; font-size: 32px; animation-duration: 24s; animation-delay: 9s; transform: translateX(3vw); }


/* --- ESTILO PARA AS JANELAS (CARDS FLUTUANTES) --- */
.app-window {
    padding: 30px;
    border-radius: 15px;
    background-color: rgba(255, 255, 255, 0.4);
    box-shadow:
            0 8px 20px rgba(0, 0, 0, 0.5),
            0 0 10px rgba(255, 255, 255, 0.5);
    text-align: center;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    border: 1px solid rgba(255, 255, 255, 0.8);
    animation: float 7s ease-in-out infinite alternate;
    position: relative;
    z-index: 2;
}

.windows-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    margin-top: 30px;
    max-width: 1000px;
    margin: 30px auto 0 auto;
}

@keyframes float {
    0% { transform: translateY(0px) rotate(1deg); }
    100% { transform: translateY(-12px) rotate(-1deg); }
}

.app-window:hover {
    transform: scale(1.05) translateY(-6px);
    background-color: rgba(255, 255, 255, 0.6);
    border-color: #ffffff;
}

.windows-grid .app-window:nth-child(2) { animation-delay: 1.5s; }
.windows-grid .app-window:nth-child(3) { animation-delay: 3s; }
.windows-grid .app-window:nth-child(4) { animation-delay: 4.5s; }

